/*---------------------------
	List Style 
---------------------------*/

ul,
ol {
    padding-left: 18px;
}

ul {
    list-style: square;
    margin-bottom: 30px;
    padding-left: 20px;
}

ul.liststyle.bullet li {
    font-size: 18px;
    line-height: 30px;
    color: var(--color-body);
    position: relative;
    padding-left: 30px;
}

@media only screen and (max-width: 767px) {
    ul.liststyle.bullet li {
        padding-left: 19px;
    }
}

ul.liststyle.bullet li::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background: var(--color-body);
    left: 0;
    top: 10px;
}

ul.liststyle.bullet li+li {
    margin-top: 8px;
}

ul li {
    font-size: var(--font-size-b1);
    line-height: var(--line-height-b1);
