/* -----------------------
Timeline Area  
--------------------------*/

.timeline-line {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 4px;
    height: 100%;
    background: rgba(196, 196, 196, 0.22);
}

.timeline-line::before,
.timeline-line::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
}

.timeline-line::before {
    height: 100%;
    bottom: 0;
    background: linear-gradient(180deg, var(var(--color-blackest)), rgba(19, 19, 19, 0));
}

.timeline-line::after {
    height: 200px;
    top: 0;
    background: linear-gradient(180deg, var(var(--color-blackest)), rgba(19, 19, 19, 0));
}

.timeline-dot {
    position: absolute;
    top: 50%;
    left: 0;
}

.timeline-dot::before,
.timeline-dot::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.timeline-dot::before {
    z-index: 1;
    width: 2.1rem;
    height: 2.1rem;
    border-radius: 50%;
    background: linear-gradient(180deg, var(--color-primary-light), var(--color-primary));
    box-shadow: 0 0 15px rgba(82, 56, 255, 0.5);
}

.timeline-dot::after {
    border: 5px solid var(var(--color-blackest));
    background: radial-gradient(50% 50% at 50% 50%, var(--color-lessdark) 48.96%, var(--color-lessdark) 100%);
    z-index: 0;
    width: 31px;
    height: 31px;
}

.timeline-dot .time-line-circle {
    position: absolute;
    top: 50%;
    border-radius: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    background: radial-gradient(50% 50% at 50% 50%, rgba(242, 201, 76, 0) 40.62%, rgba(82, 56, 255, 0.5) 100%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    will-change: transform;
    -webkit-animation: radialanimation 2.2s cubic-bezier(0.26, 1.04, 0.54, 1) infinite;
    animation: radialanimation 2.2s cubic-bezier(0.26, 1.04, 0.54, 1) infinite;
}

