.bg-overlay {
    position: relative;
}

.bg-overlay::before {
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 1;
    top: 0;
    left: 0;
    background-color: transparent;
    background-image: linear-gradient(90deg, #060606 0%, #06060600 90%);
    content: "";
}

.bg-overlay-solid {
    position: relative;
    z-index: 1;
}

.bg-overlay-solid::before {
    position: absolute;
    height: 100%;
    width: 40%;
    opacity: 1;
    top: 0;
    left: 0;
    content: "";
    background: var(--color-dark);
    z-index: -1;
}

.bg-overlay-solid::after {
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 1000px;
    opacity: 0.5;
    filter: blur(100px);
    left: auto;
    top: -250px;
    left: -250px;
    bottom: auto;
    background-image: linear-gradient(45deg, var(--color-secondary), var(--color-tertiary));
    content: "";
    z-index: -1;
}

