/* Advance Tab Style Four  */

.advance-tab-four {
    position: relative;
    padding: 48px;
    grid-column-gap: 48px;
    grid-row-gap: 48px;
    -ms-grid-rows: auto;
    grid-template-rows: auto;
    border: 1px solid var(--color-border);
    border-radius: 12px;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .advance-tab-four {
        padding: 20px;
    }
}

@media only screen and (max-width: 767px) {
    .advance-tab-four {
        padding: 20px;
    }
}

.advance-tab-four .advance-tab-top .description {
    font-size: var(--font-size-b3);
}

.rn-info-box {
    margin-top: 20px;
    background-color: var(--color-extra06);
    padding: 40px;
    border: 1px solid var(--color-border);
    border-radius: 12px;
}

.rn-info-box .box-inner .content {
    margin-top: 10px;
}

.service-thumbnail-offset-2 {
    margin-right: -60px;
    margin-bottom: -60px;
    border-radius: 10px 0 10px 0;
}

.inner.Collaborate-inner {
    padding: 0 50px 0 30px;
}

@media only screen and (max-width: 767px) {
    .inner.Collaborate-inner {
        padding: 30px 0;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .inner.Collaborate-inner .description {
        font-size: 14px;
    }
}

.social-icons {
    display: flex;
    gap: 20px;
}

.social-icons i {
    font-size: 20px;
}





.carousel-container {
    position: relative;
}

.carousel-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    font-size: 24px;
    color: #333;
    cursor: pointer;
    z-index: 2;
}

.prev {
    left: 30px;
}

.next {
